import React from 'react'
import { useContext } from "react";
import { AppContext } from "../../context/auth.context";
import {Button} from  'react-bootstrap'
import { Link } from "react-router-dom";
import "./MyRecipeList.css";

function MyRecipeList() {


    const { 

        myRecipe, setMyRecipe, handleDelete
      }  = useContext(AppContext)

console.log(myRecipe)
  return (
    <div className="flex">
    <div className='btnLeft'>
    <Button id='add' href='/myrecipes/add' > Add personal recipe</Button>
</div>
<div className='flex'>
    {
      myRecipe.map((recipe) => {
      
    return (
      
      <div key={recipe._id} className="space">
      <Link to={`/recipe/${recipe.RecipeId._id}`} >
      <img src={`${recipe.RecipeId.image}`}></img>
      <h6>{recipe.RecipeId.title}</h6>
      
      </Link>
      
      <Link to={`/myrecipes/${recipe._id}`}>
      <Button id='btnEdit' >Edit</Button></Link>
      <Button id='btnDelete' onClick={(event) => {handleDelete(event, recipe._id) }}  >delete</Button>

      </div>
    )
  })
}
</div>
</div>
  )
}

export default MyRecipeList