import React from 'react'
import "./Banner.css"

function Banner() {
  return (
    <section id="banner">

    {/* <!--
        ".inner" is set up as an inline-block so it automatically expands
        in both directions to fit whatever's inside it. This means it won't
        automatically wrap lines, so be sure to use line breaks where
        appropriate (<br />).
    --> */}
    <div class="inner">

        <header>
            <h2>FOODMATE</h2>
        </header>
        <p>This is <strong>Foodmate</strong>, a free
        <br />
        recipe tracker
        </p>
        <footer>
            <ul class="buttons stacked">
                <li><a href="#mediabox" id="blue" class="button fit scrolly">Tell Me More</a></li>
            </ul>
        </footer>

    </div>

</section>
  )
}

export default Banner