import React from 'react'
import { useContext } from "react";
import { AppContext } from "../../context/auth.context";
import { Link } from "react-router-dom";
import "./Navigation.css"
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';



const Navigation = () => {


  const { 
    user, handleLogOut,  myRecipe
  }  = useContext(AppContext)




  return (
     
     <Navbar id='nav' bg="light" expand="lg">
    {user ? (
    <Container id='sendIt'>
      <Navbar.Brand id='colorMain' href="/">FoodMate</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="me-auto">
          <Nav.Link href="/recipes">All Recipes</Nav.Link>
          <Nav.Link href="/myrecipes"><div className='smoothout'><p className='nomargin'>My Recipes</p><p id='length'>{myRecipe.length}</p></div></Nav.Link>
          <Nav.Link href="/randomrecipes">Random Recipes</Nav.Link>
          <Nav.Link onClick={handleLogOut} href="#link">Logout</Nav.Link>
        
        </Nav>
      </Navbar.Collapse>
    </Container> 
    ) : (        
    <Container id='sendIt'>
    <Navbar.Brand id='colorMain' href="/">FoodMate</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="me-auto">
        <Nav.Link id='login' href="/signup">Sign Up</Nav.Link>
        <Nav.Link href="signin">Sign In</Nav.Link>
    
      </Nav>
    </Navbar.Collapse>
    </Container>)}
    </Navbar> 


  );
};

export default Navigation;




