import React, {useContext} from "react";
import { AppContext } from "../../context/auth.context";

import "./RandomRecipe.css";



function RandomRecipes(props) {
  const { 
    recipe,
    handleAddRecipe
  }  = useContext(AppContext)

  const shuffled = recipe.sort(() => 0.5 - Math.random());

  const onlyTen = shuffled.slice(0, 10);



console.log(onlyTen)
  return (

    
  <div className="flex">
          {
            onlyTen.map((recipe) => {
            
          return (

            <div key={recipe._id} className="space">
            <a id="aTitle" href={`/recipe/${recipe._id}`} >
            <img src={`${recipe.image}`}></img>
            <h6>{recipe.title}</h6>
            </a>
            
            
			<button id="recipeBtn"  variant="light" onClick={(event) => {handleAddRecipe(event, recipe._id) }}  >add to my recipes</button>
	
            </div>
          )
        })
      }
      
    </div>
    
  )
}

export default RandomRecipes




